/**
 * Global CSS.
 */
:root {
  --color-primary: #E9004B;
  --color-secondary: #FFF3F3;
  --color-secondary-100: #FCF8F0;
  --color-gray: #EDF5F4;
  --color-white: #fff;
  --color-body: #FDFCFC;
  --color-black: #02060C;
  --color-paragraph: #626262;
  --font-body: "Outfit", serif;
  --font-title: "Outfit", serif;
  --border-radius-xs: 4px;
  --border-radius-sm: 6px;
  --border-radius-md: 10px;
  --border-radius-lg: 15px;
  --border-radius-xl: 20px;
}

/* ===========================
// Typography
==============================*/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-body);
  font-size: 16px;
  color: var(--color-body);
  font-weight: 400;
}

p {
  font-family: var(--font-title);
  font-size: 16px;
  color: var(--color-body);
  font-weight: 400;
  line-height: 26px;
  margin-bottom: 15px;
}

/*==== heading fonts setup ====*/
h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--color-black);
  font-family: var(--font-title);
  line-height: 1.2;
  font-weight: 600;
}

h1 {
  font-size: 48px;
  line-height: 1.23;
}
@media (max-width: 767px) {
  h1 {
    font-size: 36px;
  }
}

h2 {
  font-size: 40px;
}

h3 {
  font-size: 32px;
}

h4 {
  font-size: 26px;
}

h5 {
  font-size: 22px;
}

h6 {
  font-size: 18px;
}

