/*============================ 
// Global styles
=============================*/
html {
  overflow-x: hidden !important;
  width: 100%;
  height: 100%;
  text-rendering: optimizeLegibility;
  scroll-behavior: smooth;
  vertical-align: baseline;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

main {
  position: relative;
  background-color: var(--color-white);
  transition: all 0.3s ease-in-out;
  z-index: 2;
}

ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}

a {
  color: var(--color-primary);
  display: inline-block;
  transition: all 0.3s ease-in-out;
}
a:link, a:visited, a:focus {
  text-decoration: none;
  outline: 0;
}
a:hover {
  text-decoration: none;
  color: var(--color-primary);
}

img {
  max-width: 100%;
  height: auto;
}

input:focus,
textarea:focus,
select:focus {
  outline: 0;
  box-shadow: none;
}

.section-padding {
  padding-block: 120px;
}
@media (max-width: 1199px) {
  .section-padding {
    padding: 100px 0;
  }
}
@media (max-width: 991px) {
  .section-padding {
    padding: 80px 0;
  }
}

section,
.section {
  position: relative;
}

@media (min-width: 1400px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl {
    max-width: 1330px;
  }
}
.animate__animated {
  opacity: 0;
}

.animate__animated.animate__animated-in {
  opacity: 1;
}

/* Hide elements initially */
.animate__animated {
  opacity: 0;
}

.animate__animated.animate__animated-in {
  opacity: 1;
}

/* Smooth transition for menu toggle */
.menu-toggle span {
  transition: all 0.3s ease;
}

/* Mobile menu animation */
.mobile-menu.active {
  transform: translateX(0);
}

/* Keyframes */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translateY(-30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translateX(-20px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes floatAnimation {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-15px);
  }
}
@keyframes rotateAnimation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
/* Smooth Scroll Behavior */
html {
  scroll-behavior: smooth;
}

/**
 * Utility CSS.
 */
/*======================================
// pre-loader
=====================================*/
#preloader {
  background: url("../../media/preloader.gif") rgba(255, 255, 255, 0.99) no-repeat scroll center center;
  height: 100%;
  left: 0;
  overflow: visible;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 9999999;
}

/*======================================
// Back-to-top
=====================================*/
button.scroll-to-top {
  background-color: var(--color-secondary);
  height: 0px;
  width: 0px;
  overflow: hidden;
  border-radius: 50px;
  color: transparent;
  clear: both;
  visibility: hidden;
  position: fixed;
  cursor: pointer;
  display: block;
  border: none;
  right: 50px;
  bottom: 75px;
  outline: 0 !important;
  z-index: 99;
  transition: all 0.3s ease-in-out;
}
button.scroll-to-top i::before {
  font-size: 10px;
  color: var(--color-white);
  position: relative;
  top: -2px;
}

button.scroll-to-top.show {
  display: block;
  background-color: var(--color-secondary);
  right: 25px;
  bottom: 50px;
  height: 40px;
  width: 40px;
  visibility: visible;
  box-shadow: 6px 6px 12px rgba(181, 98, 0, 0.16);
}
button.scroll-to-top.show:hover i::before {
  font-size: 10px;
  animation: toBottomFromTop 0.7s forwards;
}
button.scroll-to-top.show i {
  color: var(--color-white);
}

button.scroll-to-top.show:active {
  box-shadow: 6px 6px 12px rgba(181, 98, 0, 0.16);
}

.rt-icon::before {
  width: auto;
  margin-left: 0;
  margin-right: 0;
  line-height: unset;
}

