/**
 * Layout - Header.
 */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Navigation */
.hero-section {
  position: relative;
  background-image: url("../images/hero/herobg.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.hero-section::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  top: 0;
  width: 100%;
  height: auto;
  background-image: url("../images/hero/bottom-shapes.png");
  z-index: 1;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center bottom;
}
.hero-section .shapes {
  position: absolute;
  right: 200px;
  bottom: 650px;
  width: 150px;
  height: 204px;
}
@media only screen and (max-width: 1024px) {
  .hero-section .shapes {
    display: none;
  }
}

.nav {
  position: relative;
  z-index: 5;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 24px 0px;
  margin: 0px 58px;
}

.logo {
  font-size: 24px;
  font-weight: bold;
}

.nav-links {
  display: flex;
  gap: 80px;
}

.nav-links a {
  color: white;
  text-decoration: none;
  transition: opacity 0.3s;
}

.nav-links a:hover {
  opacity: 0.8;
}

.purchase-button-1 {
  padding: 14px 32px;
  border-radius: 50px;
  background: #ff4a13;
  color: white;
  border: none;
  font-size: 16px !important;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transition: color 0.3s;
}
.purchase-button-1:after {
  content: "";
  position: absolute;
  top: 0;
  left: 150%;
  width: 200%;
  height: 100%;
  background-image: linear-gradient(90deg, transparent, var(--link-color-active, #ffffff), transparent);
  transform: skew(-20deg);
  z-index: 1;
  transition: left 0.75s cubic-bezier(0.01, 0.56, 1, 1);
}
.purchase-button-1:hover {
  color: #fff;
}
.purchase-button-1:hover:after {
  animation: shine 0.75s cubic-bezier(0.01, 0.56, 1, 1) forwards;
}
@media only screen and (max-width: 768px) {
  .purchase-button-1 {
    display: none;
  }
}

/* Hero Content */
.hero {
  text-align: center;
  position: relative;
  padding-top: 140px;
  z-index: 2;
}
@media only screen and (max-width: 768px) {
  .hero {
    padding-top: 80px;
  }
}

.text-3d {
