.fasheno-button {
  padding: 16px 32px;
  border-radius: 50px;
  background: rgba(255, 255, 255, 0.1);
  color: white;
  border: none;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transition: color 0.3s;
}
.fasheno-button.style-1 {
  border: 1px solid rgb(78, 78, 78) !important;
  background-color: transparent;
}
.fasheno-button:after {
  content: "";
  position: absolute;
  top: 0;
  left: 150%;
  width: 200%;
  height: 100%;
  background-image: linear-gradient(90deg, transparent, var(--link-color-active, #ff4a13), transparent);
  transform: skew(-20deg);
  z-index: 1;
  transition: left 0.75s cubic-bezier(0.01, 0.56, 1, 1);
}
.fasheno-button:hover {
  color: #fff;
}
.fasheno-button:hover:after {
  animation: shine 0.75s cubic-bezier(0.01, 0.56, 1, 1) forwards;
}

@keyframes shine {
  0% {
    left: 150%;
  }
  100% {
    left: -200%;
  }
}
.cta-buttons {
  display: flex;
  gap: 24px;
  justify-content: center;
}

/* Two-Part Marquee Slider */
.marquee-slider {
  position: relative;
  bottom: -150px;
  overflow: hidden;
}
@media only screen and (max-width: 768px) {
  .marquee-slider {
    bottom: -41px;
  }
}

.marquee-container {
  display: flex;
  width: 531%;
}
@media only screen and (max-width: 768px) {
  .marquee-container {
    width: 580%;
  }
}

.marquee-container.right {
  animation: marquee-right 60s linear infinite;
}

.marquee-container.left {
  animation: marquee-left 60s linear infinite;
}

@keyframes marquee-right {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-20%);
  }
}
@keyframes marquee-left {
  0% {
    transform: translateX(-20%);
  }
  100% {
    transform: translateX(0);
  }
}
.marquee-container:hover {
  animation-play-state: paused;
}

.template-card {
