/* Mobile Menu Styles */
.menu-toggle {
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 30px;
  height: 21px;
  cursor: pointer;
}

.menu-toggle span {
  display: block;
  width: 100%;
  height: 3px;
  background-color: white;
  transition: all 0.3s;
}

.mobile-menu {
  position: fixed;
  top: 0;
  right: -100%;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4196078431);
  background-image: linear-gradient(180deg, #000000 28%, rgba(95, 99, 97, 0) 341%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: right 0.5s ease-in-out;
  z-index: 1000;
}

.mobile-menu.active {
  right: 0;
}

.mobile-menu.active {
  right: 0;
}

.mobile-menu a {
  color: white;
  text-decoration: none;
  font-size: 24px;
  margin: 20px 0;
  transition: opacity 0.3s;
}

.mobile-menu a:hover {
  opacity: 0.8;
}

.close-menu {
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 50px;
  color: white;
  cursor: pointer;
}

