    height: 187px;
  }
}
.headroom {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  transform: translateY(0);
  transition: transform 0.5s ease-in-out, background-color 0.3s ease-in-out, opacity 0.5s ease-in-out;
  will-change: transform, background-color, opacity;
  background-color: transparent;
}

.headroom--scrolled {
  background-color: rgba(0, 0, 0, 0.6);
  background-image: linear-gradient(180deg, #000000 0%, rgba(59, 59, 59, 0) 100%);
  transition: background-color 0.3s ease-in-out;
  transition: transform 0.5s ease-in-out, background-color 0.5s ease-in-out, opacity 0.5s ease-in-out;
}

.headroom-pinned {
  transform: translateY(-100%);
  transition: transform 0.5s ease-in-out, background-color 0.5s ease-in-out, opacity 0.5s ease-in-out;
  opacity: 0.9;
  transform: translateY(0);
}

.headroom-unpinned {
  transform: translateY(-100%);
  box-shadow: none;
}

.nav-links a.active {
  color: #FF6C23;
}

/**
 * Layout - Footer.
